                <router-link to="/">a-mcculley.github.io</router-link>.</p>
        </div>
        <div class="container mt-3" id="delayed" style="transition: opacity 0.5s ease; opacity: 0;">
            <p>You're still here? If I'm to continue using the path metaphor, are you camping out or something?
