</template>

<script>
    import 'bootstrap/dist/css/bootstrap.min.css';
    import 'bootstrap';
    import 'bootstrap-icons/font/bootstrap-icons.css';

    export default {
        name: "Error404",
        data: function() {
            return {
                timer: null
            }
        },
        mounted: function() {
            document.querySelector('body').style.backgroundColor = 'white';
            let showDelay = () => {
                document.querySelector('#delayed').style.opacity = '1.0';
            };
            this.timer = setTimeout(showDelay, 10000.0);
        }
    }
</script>

