},function (){var _vm=this,_c=_vm._self._c;return _c('nav',{staticClass:"navbar navbar-expand-xl navbar-light w-100",staticStyle:{"position":"fixed","top":"0","padding":"16px 0","background-color":"#FFFFFF","z-index":"2048","display":"none"},attrs:{"id":"navbar"}},[_c('div',{staticClass:"container-fluid"},[_c('p',{staticClass:"font-monospace h1 mb-0",staticStyle:{"font-size":"32px"}},[_vm._v("Austin McCulley")]),_c('button',{staticClass:"navbar-toggler",attrs:{"type":"button","data-bs-toggle":"collapse","data-bs-target":"#navbar-links","aria-controls":"navbar-links","aria-expanded":"false","aria-label":"Toggle navigation"}},[_c('span',{staticClass:"navbar-toggler-icon"})]),_c('div',{staticClass:"collapse navbar-collapse",attrs:{"id":"navbar-links"}},[_c('div',{staticClass:"navbar-nav ms-3"},[_c('a',{staticClass:"nav-link",attrs:{"href":"#section-about-me"}},[_c('i',{staticClass:"bi-person-lines-fill"}),_vm._v(" About Me")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-elevator-pitch"}},[_c('i',{staticClass:"bi-person-lines-fill"}),_vm._v(" Elevator Pitch")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-edu"}},[_c('i',{staticClass:"bi-mortarboard"}),_vm._v(" Education")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-languages"}},[_c('i',{staticClass:"bi-book"}),_vm._v(" Languages")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-skills"}},[_c('i',{staticClass:"bi-lightbulb-fill"}),_vm._v(" Skills")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-coursework"}},[_c('i',{staticClass:"bi-backpack"}),_vm._v(" Coursework")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-projects"}},[_c('i',{staticClass:"bi-code-slash"}),_vm._v(" Projects")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-work-exp"}},[_c('i',{staticClass:"bi-briefcase"}),_vm._v(" Work Experience")]),_c('a',{staticClass:"nav-link",attrs:{"href":"#section-contact"}},[_c('i',{staticClass:"bi-chat-left-dots"}),_vm._v(" Contact")]),_c('a',{staticCl