},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"section container",staticStyle:{"margin-top":"100vh"}},[_c('a',{staticClass:"anchor",attrs:{"id":"section-about-me"}}),_c('h2',[_c('i',{staticClass:"bi-person-lines-fill"}),_vm._v(" About Me")]),_c('h3',[_vm._v("Overview")]),_c('p',[_vm._v("Austin is a recent graduate from the University of Michigan College of Engineering with a degree in Computer Science and a minor in Political Science. During his time studying Computer Science, he didn't focus on any particular subfields. The subfield he has the most experience with is front end development, though he is very interested in AI and Programming Language theory. Beyond that, he is a jack of all trades. In his Political Science minor, he focused on the Political Theory and American Politics subfields. For a more detailed description of his education, see the "),_c('a',{attrs:{"href":"#section-edu"}},[_vm._v("education")]),_vm._v(" and "),_c('a',{attrs:{"href":"#section-coursework"}},[_vm._v("coursework")]),_vm._v(" sections of this page.")]),_c('p',[_vm._v("Additionally, Austin served as an Instructional Aide for the course “EECS 281 Data Structures and Algorithms” at the University of Michigan Ann Arbor for 6 semesters. During Fall and Winter semesters, the course in question would have around 1000 students. Responsibilities for this position included teaching a lab lecture, holding office hours to help students debug, and involvement in the whole exam process (writing questions, writing instructor's solutions, proctoring, grading). Some noteworthy experiences included volunteering - sometimes on very short notice - to lead the grading teams multiple times, mentoring new staff for the 5 semesters following his first, and receiving an average anonymous feedback score of 5/5 on his final two semesters of teaching (with an above average response rate). Additionally, Austin has privately tutored since graduating, with around 30 total students and an average review score of 5 stars. He has primarily tutored introductory college computer science, with some high school students and some more advanced collegiate computer science. A more detailed description of the responsibilities associated with both positions is available on his resume and in the "),_c('a',{attrs:{"href":"#section-work-exp"}},[_vm._v("work experience")]),_vm._v(" section of this page.")]),_c('p',[_vm._v("Austin is seeking work in a Software Engineering position, and is open to work in any subfield (Front End, Back End, Full Stack, etc).")]),_c('p',[_vm._v("Finally, I would like to talk a bit about myself outside of the sanitized, technical pitch I have given thus far. Let you get to actually know me a bit.")]),_c('p',[_vm._v("I am a proud first generation college student. My mother works at an appliance store, as a salesperson, and my father was never in my life. Growing up, my family was consistently below the poverty line. I don't say this all to elicit sympathy, but instead because I want to emphasize my pride in myself getting thus far. That isn't to deny that my background had struggles; many times my family didn't have all that it wanted financially growing up, navigating college as the only one in my family to attend, the difficulty in adjusting to college, and more. However, what I want to express is the hard work, the skill, the tenacity it took in order to achieve my level of success and get to this position in spite of all that. I am a highly focused, dedicated person and I will do whatever it takes to succeed.")]),_c('p',[_vm._v("I'm not all work, all the time though. I think a solid work life balance is key; it both makes for a happier life and more productive time at work. In my free time I like reading and playing video games, as many nerds and computer scientists surely do as well. I strongly value my relationships in life, both my close friends and family, so I take just about any chance I can to spend time with the people in my life, whether that's just me and one other silently working in the same room or a large group heading out to breakfast together. Since I view my relationships as what makes life valuable, I try to behave with as much kindness as possible to those in my life, which I think has contributed to my empathy in interacting with new people as well. I'm also pretty big on podcasts and music, with my musical interest primarily being 1960s-1990s rock and roll. I also spend a fair amount of my free time writing for fun, I think one relatively unique thing about me is I'm a massive movie buff, I even made my own version of 1000 Films to See Before You Die! Additionally, I love combining my love for film and my closeness with friends. For a long time I used to host weekly movie nights with my friends. Nowadays, it's less frequent but we still do it. During those movie nights, I make either my (secret) hot cocoa recipe or my (secret) milkshake recipe, depending on the time of year. I, of course, also have a stock of popcorn and popcorn toppings that get busted out during the movie nights. Additionally, me and a group of my closest friends have an ongoing RPG campaign (I'm a half elf warrior mage, it's an excellent time). Finally, I must admit, during my time as an Instructional Aide and Tutoring, I've discovered a great passion for teaching, so I hope to, at some point in the future, pick up teaching Computer Science again at some level.")]),_c('p',[_vm._v("While I know I lack experience, as I have clearly articulated, I am highly adaptable, have strong fundamentals, and have excellent communication skills, all of which will serve to make me an asset to your organization. You will be hard pressed to find a more dedicated, hard working individual.")]),_c('p',[_vm._v("For a quick elevator pitch, see "),_c('a',{attrs:{"href":"#section-elevator-pitch"}},[_vm._v("Elevator Pitch.")]),_vm._v(" For details on my spoken languages, see "),_c('a',{attrs:{"href":"#section-languages"}},[_vm._v("Languages.")]),_vm._v(" For Skills (Programming Languages, Tools, Hard Skills, and Soft Skills), see "),_c('a',{attrs:{"href":"#section-skills"}},[_vm._v("Skills.")]),_vm._v(" For my educational background, see "),_c('a',{attrs:{"href":"#section-edu"}},[_vm._v("Education.")]),_vm._v(" For a detailed breakdown on my coursework, see "),_c('a',{attrs:{"href":"#section-coursework"}},[_vm._v("Coursework.")]),_vm._v(" For my personal projects, see "),_c('a',{attrs:{"href":"#section-projects"}},[_vm._v("Projects.")]),_vm._v(" For my work experience, see "),_c('a',{attrs:{"href":"#section-work-exp"}},[_vm._v("Work Experience.")]),_vm._v(" For my contact information, see "),_c('a',{attrs:{"href":"#section-contact"}},[_vm._v("Contact Me.")]),_vm._v(" For my resume and other downloadable documents, see "),_c('a',{attrs:{"href":"#section-resume"}},[_vm._v("Resume.")])]),_c('p',[_vm._v("Thank you for reading this far, do not hesitate to contact me with any questions or offers. I look forward to working with you.")]),_c('p',[_vm._v("Finally, though it's included in my "),_c('a',{attrs:{"href":"#section-contact"}},[_vm._v("Contact Me")]),_vm._v(" section, I will reiterate my contact information for your convenience:")]),_c('p',[_c('i',{staticClass:"bi-telephone"}),_vm._v(" Phone Number: (810) 358-4585")]),_c('p',[_c('i',{staticClass:"bi-envelope"}),_vm._v(" Email 1: "),_c('a',{attrs:{"href":"mailto:amcculley19@gmail.com","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("amcculley19@gmail.com")])]),_c('p',[_c('i',{staticClass:"bi-envelope"}),_vm._v(" Email 2: "),_c('a',{attrs:{"href":"mailto:mcculley@umich.edu","target":"_blank","rel":"noopener noreferrer"}},[_vm._v(" mcculley@umich.edu")])]),_c('p',[_c('i',{staticClass:"bi-linkedin"}),_vm._v(" LinkedIn: "),_c('a',{attrs:{"href":"https://www.linkedin.com/in/a-mcculley/","target":"_blank"}},[_vm._v(" linkedin.com/a-mcculley")])]),_c('p',[_c('i',{staticClass:"bi-github"}),_vm._v(" Github:"),_c('a',{attrs:{"href":"https://gi