},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"section container"},[_c('a',{staticClass:"anchor",attrs:{"id":"section-elevator-pitch"}}),_c('h2',[_c('i',{staticClass:"bi-person-lines-fill"}),_vm._v(" Elevator Pitch")]),_c('p',[_vm._v("I would like to highlight 3 attributes in particular that I feel let me stand out from the crowd and allow me to be an asset in any organization.")]),_c('p',[_vm._v("Firstly, there's my breadth of knowledge. During my undergraduate study, I took a wide variety of courses in a large number of different subfields. AI, Programming Language theory, UI Development, Software Engineering, Computational Linguistics, and even Quantum Information Science, to name just a few of the subjects I dabbled in. While this has meant that I am not a master of any specific field, it does mean that I am a jack of all trades and can be competently placed in a wide variety of positions. This experience, jumping between so many different areas, has also made me able to very quickly learn. I have learned how to learn quickly and adapt as necessary to new expectations. Additionally, this wide variety of courses has led me to learn a large variety of languages and become proficient in a wide variety of tools, a list of which you can find on my resume or in the "),_c('a',{attrs:{"href":"#section-skills"}},[_vm._v("Skills ")]),_vm._v("section of this page.")]),_c('p',[_vm._v("Secondly, I have also built strong fundamentals in computer science. My role as an Instructional Aide for EECS 281 helped me build extremely strong fundamentals, as that course is considered the keystone course of the University of Michigan's computer science department, with success in the course allowing students to take upper level courses. Teaching this foundational course for 6 semesters ingrained strong fundamentals. Another example of how I've built such strong fundamentals is simply how long I have been coding, with my first computer science experience being in the Fall of 2015. Finally, my experience as a private tutor is another thing that has deepened my skill at the fundamentals, as I have taught students with a wide variety of computer science backgrounds at a number of different course levels, which has further strengthened my skills in the fundamentals.")]),_c('p',[_vm._v("Finally, I have developed exceptional communication skills, both written and oral. I have used a wide variety of strategies in order to do so. Firstly, I would like to highlight my Political Science minor, which is unique for a Computer Science major. Over the course of completing my coursework, I wrote many essays on a wide variety of topics and positions (also developing substantial research skills), which led to the development of a high degree of skill in rhetoric. Additionally, during my coursework, I had to do a large number of presentations, which developed my public speaking skills, though my other experiences showcase my abilities in that regard more, with my experience as an Instructional Aide as the most important example. As previously discussed, EECS 281 is a foundational course, and it is one that students often find difficult, so teaching it for so long with such efficacy has taught me a wide range of public speaking skills and strategies in order to best reach as many students as possible. As opposed to the rhetorical focus of my Political Science minor, my experience as an Instructional Aide was instead focused on technical communication. Additionally, in my experience teaching as an Instructional Aide, I had to learn how to communicate effectively with people from a number of different backgrounds, something that I take particular pride in. A final example of my skill in communication is my experience as a private tutor, which is very similar to my experiences as an Instructional Aide. One aspect that I want to highlight about my tutoring experience that is more prominent than it was as an Instructional Aide is that tutoring has improved my skills at communicating with people at different levels of ability, even when talking about highly complicated subjects. Another thing I want to lightly touch on is my leadership skills, which I view as a subset of my broader communication skills. Over the course of my experience Instructional Aide, I took a leadership role multiple times; I led grading teams several times, once even asked on the day of the exam to do so and also mentored new staff every semester - aside from my first of course.")])])
