<template>
  <router-view>
    <Homepage/>
  </router-view>
</template>

<script>

  import 'bootstrap/dist/css/bootstrap.min.css';
  import 'bootstrap';
  import 'bootstrap-icons/font/bootstrap-icons.css';
  import Homepage from "@/pages/Homepage";

  export default {
    name: 'App',
    components: {
      Homepage
    },
    mounted: function() {
      let scrollHandler = () => {
        let scrollAmount = window.scrollY;
        console.log(`scrollAmount ${scrollAmount}`);
        // hello world label: 0 through 288
        if (scrollAmount <= 288) {
          document.getElementById('helloworld').style.display = 'inherit';
          document.getElementById('navbar').style.display = 'none';
          document.getElementById('profile').style.display = 'inherit';
          let scrolledRatio = (scrollAmount) / 288;
          let fullBgColor = [0, 16, 28];
          let fullColor = [175, 212, 233]
          document.getElementById('helloworld').style.color = `rgb(${(scrolledRatio * (fullBgColor[0] - fullColor[0])) + fullColor[0]},${(scrolledRatio * (fullBgColor[1] - fullColor[1])) + fullColor[1]},${(scrolledRatio * (fullBgColor[2] - fullColor[2])) + fullColor[2]})`;
          document.querySelector('body').style.backgroundColor = `rgb(${(scrolledRatio * (fullColor[0] - fullBgColor[0])) + fullBgColor[0]},${(scrolledRatio * (fullColor[1] - fullBgColor[1])) + fullBgColor[1]},${(scrolledRatio * (fullColor[2] - fullBgColor[2])) + fullBgColor[2]})`;
          document.getElementById('section-about-me').style.marginTop =
                  `calc(${Math.min(100,200*(1-scrolledRatio))}vh + 16 px)`;
          document.getElementById('helloworld').style.fontSize = `${Math.max(32,112 - (scrollAmount) / 288 * 96)}px`;
          document.getElementById('profile').style.opacity = 1 - scrolledRatio;
          document.getElementById('profile').style.height = `${(1 - scrolledRatio) * 75}vh`;
          if (scrolledRatio > 0.5) {
            document.getElementById('helloworld').innerHTML = "Austin McCulley";
          } else {
            document.getElementById('helloworld').innerHTML = "Austin McCulley";
          }

        } else {
          document.getElementById('helloworld').style.display = 'none';
          document.getElementById('navbar').style.display = 'inherit';
          document.querySelector('body').style.backgroundColor = "#00101c";
          document.getElementById('profile').style.display = 'none';
        }
        // 288 through 416 (128px): opacity of other navbar items as well as background color
        if (scrollAmount >= 288 && scrollAmount <= 416) {
            let scrolledRatio = (scrollAmount - 288) / 128;
            let fullColor = [175, 212, 233]; // afd4e9ff
            let textColor = [175, 212, 233];
            let fullTextColor = [246, 253, 255];
            document.querySelector('.navbar').style.backgroundColor = `rgb(${fullColor[0]},${fullColor[1]},${fullColor[2]})`;
            document.querySelector('.navbar').style.color = `rgb(${(scrolledRatio * (fullTextColor[0] - textColor[0])) + textColor[0]},${(scrolledRatio * (fullTextColor[1] - textColor[1])) + textColor[1]},${(scrolledRatio * (fullTextColor[2] - textColor[2])) + textColor[2]})`;
            document.querySelector('#navbar-links').style.opacity = `${scrolledRatio}`;
            
        } else if (scrollAmount > 352) {
            document.querySelector('.navbar').style.backgroundColor = `rgb(175,212,233)`;
            document.querySelector('#navbar-links').style.opacity = `1.0`;
            
        }
      };

      scrollHandler();

      document.addEventListener('scroll', scrollHandler);
    }
  }
</script>

