        <div class="section container" style="margin-top: 100vh;">
            <a class="anchor" id="section-about-me"/>
            <h2><i class="bi-person-lines-fill"/> About Me</h2>
            <h3>Overview</h3>
            <p>Austin is a recent graduate from the University of Michigan College of Engineering with a degree in Computer Science and a minor in Political Science. During his time studying Computer Science, he didn't focus on any particular subfields. The subfield he has the most experience with is front end development, though he is very interested in AI and Programming Language theory. Beyond that, he is a jack of all trades. In his Political Science minor, he focused on the Political Theory and American Politics subfields. For a more detailed description of his education, see the <a href="#section-edu">education</a> and <a href="#section-coursework">coursework</a> sections of this page.</p>
            <p>Additionally, Austin served as an Instructional Aide for the course “EECS 281 Data Structures and Algorithms” at the University of Michigan Ann Arbor for 6 semesters. During Fall and Winter semesters, the course in question would have around 1000 students. Responsibilities for this position included teaching a lab lecture, holding office hours to help students debug, and involvement in the whole exam process (writing questions, writing instructor's solutions, proctoring, grading). Some noteworthy experiences included volunteering - sometimes on very short notice - to lead the grading teams multiple times, mentoring new staff for the 5 semesters following his first, and receiving an average anonymous feedback score of 5/5 on his final two semesters of teaching (with an above average response rate). Additionally, Austin has privately tutored since graduating, with around 30 total students and an average review score of 5 stars. He has primarily tutored introductory college computer science, with some high school students and some more advanced collegiate computer science. A more detailed description of the responsibilities associated with both positions is available on his resume and in the <a href="#section-work-exp">work experience</a> section of this page.</p>
            <p>Austin is seeking work in a Software Engineering position, and is open to work in any subfield (Front End, Back End, Full Stack, etc).</p>
            <p>Finally, I would like to talk a bit about myself outside of the sanitized, technical pitch I have given thus far. Let you get to actually know me a bit.</p>
            <p>I am a proud first generation college student. My mother works at an appliance store, as a salesperson, and my father was never in my life. Growing up, my family was consistently below the poverty line. I don't say this all to elicit sympathy, but instead because I want to emphasize my pride in myself getting thus far. That isn't to deny that my background had struggles; many times my family didn't have all that it wanted financially growing up, navigating college as the only one in my family to attend, the difficulty in adjusting to college, and more. However, what I want to express is the hard work, the skill, the tenacity it took in order to achieve my level of success and get to this position in spite of all that. I am a highly focused, dedicated person and I will do whatever it takes to succeed.</p>
            <p>I'm not all work, all the time though. I think a solid work life balance is key; it both makes for a happier life and more productive time at work. In my free time I like reading and playing video games, as many nerds and computer scientists surely do as well. I strongly value my relationships in life, both my close friends and family, so I take just about any chance I can to spend time with the people in my life, whether that's just me and one other silently working in the same room or a large group heading out to breakfast together. Since I view my relationships as what makes life valuable, I try to behave with as much kindness as possible to those in my life, which I think has contributed to my empathy in interacting with new people as well. I'm also pretty big on podcasts and music, with my musical interest primarily being 1960s-1990s rock and roll. I also spend a fair amount of my free time writing for fun, I think one relatively unique thing about me is I'm a massive movie buff, I even made my own version of 1000 Films to See Before You Die! Additionally, I love combining my love for film and my closeness with friends. For a long time I used to host weekly movie nights with my friends. Nowadays, it's less frequent but we still do it. During those movie nights, I make either my (secret) hot cocoa recipe or my (secret) milkshake recipe, depending on the time of year. I, of course, also have a stock of popcorn and popcorn toppings that get busted out during the movie nights. Additionally, me and a group of my closest friends have an ongoing RPG campaign (I'm a half elf warrior mage, it's an excellent time). Finally, I must admit, during my time as an Instructional Aide and Tutoring, I've discovered a great passion for teaching, so I hope to, at some point in the future, pick up teaching Computer Science again at some level.</p>
            <p>While I know I lack experience, as I have clearly articulated, I am highly adaptable, have strong fundamentals, and have excellent communication skills, all of which will serve to make me an asset to your organization. You will be hard pressed to find a more dedicated, hard working individual.</p>
            <p>For a quick elevator pitch, see <a href="#section-elevator-pitch">Elevator Pitch.</a> For details on my spoken languages, see <a href="#section-languages">Languages.</a> For Skills (Programming Languages, Tools, Hard Skills, and Soft Skills), see <a href="#section-skills">Skills.</a> For my educational background, see <a href="#section-edu">Education.</a> For a detailed breakdown on my coursework, see <a href="#section-coursework">Coursework.</a> For my personal projects, see <a href="#section-projects">Projects.</a> For my work experience, see <a href="#section-work-exp">Work Experience.</a> For my contact information, see <a href="#section-contact">Contact Me.</a> For my resume and other downloadable documents, see <a href="#section-resume">Resume.</a></p>
            <p>Thank you for reading this far, do not hesitate to contact me with any questions or offers. I look forward to working with you.</p>
            <p>Finally, though it's included in my <a href="#section-contact">Contact Me</a> section, I will reiterate my contact information for your convenience:</p>
            <p><i class="bi-telephone"/> Phone Number: (810) 358-4585</p>
