<script>

    import 'bootstrap/dist/css/bootstrap.min.css';
    import 'bootstrap';
    import 'bootstrap-icons/font/bootstrap-icons.css';
    import CourseCard from "@/components/CourseCard";
    import ProjectInformation from "@/components/ProjectInformation";

    export default {
        name: 'Homepage',
        components: {
            CourseCard,
            ProjectInformation
        },
        mounted: function() {

            let beginHelp = () => {
                document.getElementById(`scroll-down`).style.opacity = 1.0
                this.helperInterval = setInterval(() => {
                    document.getElementById('scroll-down').style.bottom = (this.cycle === 1 || this.cycle === 3 ?
                        '24px' : '32px');
                    this.cycle++;
                    this.cycle %= 10;
                }, 250);

            };

            let scrollHandler = () => {

                if (this.helperInterval == null) {
                    this.helperInterval = setTimeout(beginHelp, 500);
                }

                let scrollAmount = window.scrollY;

                if (scrollAmount > 64) {
                    clearInterval(this.helperLightInterval);
                    document.getElementById(`scroll-down`).style.bottom = '32px';
                    document.getElementById(`scroll-down`).style.opacity = '0';
                }
                else
                {
                    beginHelp();
                }

                console.log(`scrollAmount ${scrollAmount}`);
                // hello world label: 0 through 288
                if (scrollAmount <= 288) {
                    document.getElementById('helloworld').style.display = 'inherit';
                    document.getElementById('navbar').style.display = 'none';
                    let scrolledRatio = (scrollAmount) / 288;
                    let fullBgColor = [0, 16, 28];
                    let fullColor = [175, 212, 233]
                    document.getElementById('helloworld').style.color = `rgb(${(scrolledRatio * (fullBgColor[0] - fullColor[0])) + fullColor[0]},${(scrolledRatio * (fullBgColor[1] - fullColor[1])) + fullColor[1]},${(scrolledRatio * (fullBgColor[2] - fullColor[2])) + fullColor[2]})`;
                    document.querySelector('body').style.backgroundColor = `rgb(${(scrolledRatio * (fullColor[0] - fullBgColor[0])) + fullBgColor[0]},${(scrolledRatio * (fullColor[1] - fullBgColor[1])) + fullBgColor[1]},${(scrolledRatio * (fullColor[2] - fullBgColor[2])) + fullBgColor[2]})`;
                    document.getElementById('profile').style.opacity = 1 - scrolledRatio;
          document.getElementById('profile').style.height = `${(1 - scrolledRatio) * 75}vh`;
                    document.getElementById('section-about-me').style.marginTop =
                        `calc(${Math.min(100,200*(1-scrolledRatio))}vh + 16 px)`;
                    document.getElementById('helloworld').style.fontSize = `${Math.max(32,112 - (scrollAmount) / 288 * 96)}px`;

                    if (scrolledRatio > 0.5) {
                        document.getElementById('helloworld').innerHTML = "Austin McCulley";
                    } else {
                        document.getElementById('helloworld').innerHTML = "Austin McCulley";
                    }

                } else {
                    document.getElementById('helloworld').style.display = 'none';
                    document.getElementById('navbar').style.display = 'inherit';
                    document.querySelector('body').style.backgroundColor = "#000000";
                }
                // 288 through 416 (128px): opacity of other navbar items as well as background color
                if (scrollAmount >= 288 && scrollAmount <= 416) {
                    let scrolledRatio = (scrollAmount - 288) / 128;
                    let fullColor = [175, 212, 233]; // afd4e9ff
                    let textColor = [175, 212, 233];
                    let fullTextColor = [246, 253, 255];
                    document.querySelector('.navbar').style.backgroundColor = `rgb(${fullColor[0]},${fullColor[1]},${fullColor[2]})`;
                    document.querySelector('.navbar').style.color = `rgb(${(scrolledRatio * (fullTextColor[0] - textColor[0])) + textColor[0]},${(scrolledRatio * (fullTextColor[1] - textColor[1])) + textColor[1]},${(scrolledRatio * (fullTextColor[2] - textColor[2])) + textColor[2]})`;
                    document.querySelector('#navbar-links').style.opacity = `${scrolledRatio}`;
                } else if (scrollAmount > 352) {
                    document.querySelector('.navbar').style.backgroundColor = `rgb(175,212,233)`;
                    document.querySelector('#navbar-links').style.opacity = `1.0`;
                }
            };

            scrollHandler();

            document.addEventListener('scroll', scrollHandler);
        },
        data: function() {
            return {

                helperInterval: null,
                cycle: 0,

                descriptions: {
                    'eecs203':
                        `<span class="bold">Course Guide Description:</span> Introduction to the mathematical foundations of computer science. Topics covered include: propositional and predicate logic, set theory, function and relations, growth of functions and asymptotic notation, introduction to algorithms, elementary combinatorics and graph theory and discrete probability theory.
                        <br>
                        <span class="bold">Syllabus Description:</span> Discrete Math --- Introduction to the mathematical foundations of computer science. Topics covered include: propositional and predicate logic, set theory and functions, elementary combinatorics, discrete probability theory, relations, graph theory, introduction to algorithms, growth of functions and asymptotic notation, and introduction to number theory.`,
                    'eecs280':
                        `<span class="bold">Course Guide Description:</span> Techniques and algorithm development and effective programming, top-down analysis, structured programming, testing, and program correctness. Program language syntax and static and runtime semantics. Scope, procedure instantiation, recursion, abstract data types, and parameter passing methods. Structured data types, pointers, linked data structures, stacks, queues, arrays, records, and trees.
                        <br>
                        <span class="bold">Personal Description:</span> Covers topics such as: value and reference semantics, functional abstraction, data abstraction, inheritance and polymorphism, dynamic resource management, compound types, encapsulation, container data structures, recursion, generic programming, higher-order functions, testing and debugging, stacks and queues, linked lists and binary trees, arrays and vectors, file and stream I/O, C and C++ strings, pointers, structs and classes, operator overloading, derived types, the big 3, templates, iterators, functors, and exceptions. 
                        <br>
                        <span class="bold">Languages and Tools:</span> C++, Valgrind, Perf, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Statistics</span>: Introductory project in order to establish rudementary understanding of programming concepts; solves for median, mean, and mode of data sets.</li>
                        <li><span class="bold">Image Seam Carving</span>: Implemented a seam carving algorithm in order to reduce image size without losing important information.</li>
                        <li><span class="bold">Euchre</span>: implementation of a game of Euchre, with various parameters able to be specified via command line arguments. Tested understanding of and ability to implement inheritance in C++ classes, as well as polymorphism.</li>
                        <li><span class="bold">Web Server</span>: implementation of a REST API web server for an office hours queue. Tested understanding of and ability to implement linked lists, as well as usage of a C++ library for JSON format.</li>
                        <li><span class="bold">Machine Learning</span>: implementation of a Naive Bayesian classifier based on the bag of words model. Tested understanding of and ability to implement binary search trees, as well as usage of the <span class="font-monospace">std::map</span> class.</li>
                        </ul>`,
                    'eecs281':
                        `<span class="bold">Course Guide Description:</span> Introduction to algorithm analysis and O-notation; Fundamental data structures including lists, stacks, queues, priority queues, hash tables, binary trees, search trees, balanced trees and graphs; searching and sorting algorithms; recursive algorithms; basic graph algorithms; introduction to greedy algorithms and divide and conquer strategy. Several programming assignments.
                        <br>
                        <span class="bold">Syllabus Description:</span> EECS 281 is an introductory course in data structures and algorithms at the undergraduate level.  The objective of the course is to present a number of fundamental techniques to solve common programming problems.  For each of these problems, we will determine an abstract specification for a solution and examine one or more potential representations to implement the abstract specification, focusing on those with significant advantages in time/space required to solve large problem instances.  When appropriate, we will consider special cases of a general problem that admit particularly elegant solutions.
                        <br>
                        <span class="bold">Personal Description:</span> Course discussing several fundamental data structures (lists, stacks, queues, deques, priority queues, hash tables, binary search trees, and avl trees) and several fundamental algorithms (binary search, depth first search, depth first search, dijkstra's algorithm, prim's algorithm, kruskal's algorithm, bubble sort, insertion sort, selection sort, heap sort, quicksort, and merge sort). Additionally, discusses big O notation, and various algorithm families (brute force, greedy, divide and conquer, dynamic programming, and branch and bound).
                        <br>
                        A central theme of the course is the tradeoff between time and memory, and projects are grading in accordance to meeting threshholds in both regards (in addition to accuracy of course).
                        <br>
                        <span class="bold">Languages and Tools:</span> C++, Valgrind, Perf, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Rescue the Countess</span>: implemented BFS and DFS algorithms to solve a top down adventure game, where the player can use pipes to warp between floors while seeking out the countess.</li>
                        <li><span class="bold">Star Wars Episode X: A New Heap</span>: used priority queues to simulate battles between jedi and sith (as odd as it sounds, this project is actually a skin over a different 281 project simulating a stock market, where jedi are buyers and sith are sellers and troop numbers are number of stocks, etc), where jedi and sith have different levels of strength. Various statistics can also be printed during the simulation, including a running median (also implemented with priority queues)</li>
                        <li><span class="bold">Log Manager</span>: implementation of a simple version of log manager, dealing with various logs and timestamps. Tested understanding of and ability to use hash maps.</li>
                        <li><span class="bold">Drone Delivery</span>: implementation of MST algorithms, and a branch-and-bound search as well as a heuristic-based traveling salesperson solution.</li>
                        </ul>`,
                    'eecs370':
                        `<span class="bold">Course Guide & Syllabus Description:</span> This course is intended to give you a basic understanding of how computers execute programs. Understanding computers means understanding the hardware/software process of how you and the computer work together to have the computer carry out a concept. In your introductory programming courses (e.g. EECS 280), you learned how to express a concept in terms of a high-level programming language such as C/C++. In EECS 370, you will see how a low-level language is executed by the hardware, and you will see how to put together basic, hardware building blocks to form the functional units of a computer. To achieve these goals, you will design and "build" simple computers at various levels of detail. In this course, building will not mean connecting chips and gates. Rather, you will describe the hardware in diagrams, finite-state machines, and hardware simulators (written in C). To further your understanding of other topics, we will provide practice questions that will be discussed in the discussion sections
                        <br>
                        <span class="bold">Languages and Tools:</span> Assembly, LC2K, C, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Singlefile Assembler</span>: implementation of an assembler for LC2K, a simple assembly language. The assembler converts LC2K assembly files into machine code.</li>
                        <li><span class="bold">Multifile Assembler/Linker</span>: implementation of an assembler and linker for LC2K. The assembler converts LC2K assembly files into object files, and the linker combines object files into a machine code file.</li>
                        <li><span class="bold">Pipeline Simulator</span>: simulator for a pipeline datapath, including logic for data forwarding and branch prediction.</li>
                        <li><span class="bold">Cache Simulator</span>: simulator for a single-cycle datapath with a memory cache. Includes support for fully associative, direct mapped, and set associative caches via command line arguments.</li>
                        </ul>`,
                    'eecs493':
                        `<span class="bold">Course Guide Description:</span> Concepts and techniques for designing computer system user interfaces to be easy to learn and use, with an introduction to their implementation. Task analysis, design of functionality, display and interaction design, and usability evaluation. Interface programming using an object-oriented application framework. Fluency in a standard object-oriented programming language is assumed.
                        <br>
                        <span class="bold">Syllabus Description:</span> For students to gain practical experience with the design, implementation, and testing of user interfaces. This course will present design methods, UI abstractions, and practical examples of tools and languages commonly used in UI development. The course and project will be focused on web development for UI creation . Significant experience with object-oriented programming is assumed, and experience with web technologies is beneficial. The course will cover core concepts and methods in web programming.
                        <br>
                        <span class="bold">Languages and Tools:</span> JavaScript, CSS, HTML, jquery, Vue, git, bootstrap
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Awards Website</span>: Made a simple mock tony awards website.</li>
                        <li><span class="bold">Web Game</span>: Simple JavaScript based browser game themed around avoiding asteroids.</li>
                        <li><span class="bold">iTunes</span>: Designed a mock iTunes webstore page.</li>
                        <li><span class="bold">Rock Climbing</span>: Semester project, much larger scale than other listed projects. In a small group, designed and implemented the front end of a web app. Our group designed one that acts similarly to a dating service but was instead dedicated to finding people to rock climb with, including verifying emails as being university ones and pairing people based on a set of criteria they could provide or edit at any time.</li>
                        </ul>`,
                    'eecs481':
                        `<span class="bold">Course Guide Description:</span> Pragmatic aspects of the production of software systems, dealing with structuring principles, design methodologies and informal analysis. Emphasis is given to development of large, complex software systems.Professional problem-solving methods developed through intensive group studies. Normally one significant design project is chosen for entire class requiring multiple EECS disciplines and teams. Use of analytic, computer, design, and experimental techniques where applicable are used. Projects are often interdisciplinary allowing non-EECS seniors to also take the course (consult with instructor).
                        <br>
                        <span class="bold">Syllabus Description:</span> Successful software projects require more than just technical expertise. Figuring out what the client wants, collaborating in a team, managing complexity, mitigating risks, staying on time and budget, and determining, under various constraints, when a product is "good enough" to be shipped are equally important topics that often have a significant human component. This course explores these issues broadly and covers the fundamentals of modern software engineering and analysis. This course focuses on software engineering and analysis. At a high level, the course is organized around five core topics: Measurement and Risk, Quality Assurance (especially testing), Software Defects, Software Design, and Productivity at Scale.
                        <br>
                        <span class="bold">Languages and Tools:</span> Java, C, C++, Python, coverage package, gcov, EvoSuite, American Fuzzy Lop, CodeSonar, Infer, VirtualBox, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Test Coverage</span>: First covers basic Linux fundamentals. Secondly, using the coverage package in Python on an implementation of AVL trees. Thirdly, using gcov on libpng. Finally, using cobertura on JFreeChart.</li>
                        <li><span class="bold">Test Automation</span>: Used American Fuzzy Lop in order to test libpng again. Then used EvoSuite to test jsoup. </li>
                        <li><span class="bold">Mutation Testing</span>: Used mutation testing to evaluate the quality of test suites.</li>
                        <li><span class="bold">Defect Detection</span>: Used static analysis tools to detect defects in code. In particular used CodeSonar and Infer.</li>
                        <li><span class="bold">Debugging Automation</span>: Implemented a delta debugging algorithm and coverage based fault localization in order to automate debugging insofar is possible.</li>
                        <li><span class="bold">Contribution</span>: Made a non-trivial submission to an ongoing open source project of substantial size.</li>
                        </ul>`,
                    'eecs390':
                        `<span class="bold">Course Guide Description:</span> Survey of programming language features and paradigms and how to effectively use them. Introduces common features for program execution, data, and resource management. Exploration of paradigms including imperative, functional, object-oriented, and declarative programming, as well as advanced programming techniques. Students will gain experience in large projects that incorporate these paradigms.
                        <br>
                        <span class="bold">Syllabus Description:</span> Survey of programming language features and paradigms, with a focus on how to effectively use them. Introduces common features for structuring program execution, data, and resource management. Exploration of programming paradigms including imperative, functional, object-oriented, and declarative paradigms, as well as advanced techniques such as metaprogramming. Students will gain programming experience in large projects that incorporate these paradigms and techniques.
                        <br>
                        <span class="bold">Personal Description:</span> Goes through several programming languages and programming language families (Object Oriented, Functional, Logic)
                        <br>
                        <span class="bold">Languages and Tools:</span> Python, Scheme, Prolog, C, uC, C++, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">PageRank</span>: Implemented the PageRank algorithm in Python.</li>
                        <li><span class="bold">Scheme Lexer and Parser</span>: Designed a simple lexer and parser in scheme.</li>
                        <li><span class="bold">Scheme Metacircular Evaluator</span>: Wrote an evaluator for scheme in scheme.</li>
                        <li><span class="bold">Semantic Analysis</span>: For uC, go through the AST to resolve types and ensure all functions and variables are declared. Other simple compiler behaviors also followed, such as ensuring break and continue keywords only occur in valid contexts.</li>
                        <li><span class="bold">Code Generation</span>: Building upon the previous project, wrote a code generator for uC, specifically one that would take uC code and generate equivalent C++ code.</li>
                        </ul>`,
                    'eecs490':
                        `<span class="bold">Course Guide Description:</span> Fundamental concepts in programming languages. Course covers different programming languages including functional, imperative, object-oriented, and logic programming languages; different programming language features for naming, control flow, memory management, concurrency, and modularity; as well as methodologies, techniques and tools for writing correct and maintainable programs.
                        <br>
                        <span class="bold">Syllabus Description:</span> Programming languages are rich mathematical structures and powerful user interfaces. This course covers the design and use of modern programming languages. We will build up systematically from formal first principles while considering human factors in language design, language prototyping, and techniques for reasoning precisely about program behavior throughout the course. The goal is to change the way you think about programming and programming language design in ways that will remain relevant across the many technology hype cycles that you will encounter over the course of your careers.
                        <br>
                        <span class="bold">Personal Description:</span> As opposed to Programming Paradigms, which touches upon a wide variety of programming languages that exist, this course is focused on the theory behind programming languages, proofs, etc. 
                        <br>
                        <span class="bold">Languages and Tools:</span> OCaml
                        <br>
                        <span class="bold">Projects:</span> While there are no large projects in this class, throughout many small homework assignments, we implemented our own functional language in OCaml.`,
                    'eecs496':
                        `<span class="bold">Course Guide Description:</span> Design principles for multidisciplinary team projects, team strategies, entrepreneurial skills, ethics, social and environmental awareness, and life long learning. 
                        <br>
                        <span class="bold">Personal Description:</span> A course for software engineers focused on professionalism and professional life, featuring topics like budgeting, finance, how to act in various formal situations (such as formal dinners), etc.`,
                    'eecs298':
                        `<span class="bold">Syllabus Description:</span> The failures of classical theories to explain important physical phenomena led to revolutionary and unprecedented changes in our thinking, and, in turn, to the development of quantum mechanics in the first half of the twentieth century. It was long believed that information processing and computing were solely mathematical constructs and as such were independent of nature and the laws of quantum mechanics. In the 1980's this assumption was found to be untrue, and the consequences have been profound. The introduction of quantum mechanics into communications and computation has produced new paradigms (quantum information and computation) and some unforeseen results in the fields of computation, communications and learning. For example, quantum algorithms have now been found for factoring composite numbers (Shor's algorithms 1994). In contrast, there are no known practical (i.e., polynomial time) classical solutions for the problem. This course is an introduction to this general area. No prior knowledge of quantum mechanics, classical computing or information is assumed.
                        <br>
                        <span class="bold">Languages and Tools:</span> Python, QISKIT`,
                    'eecs492':
                        `<span class="bold">Course Guide Description:</span> Introduction to the core concepts of AI, organized around building computational agents. Emphasizes the application of AI techniques. Topics include search, logic, knowledge representation, reasoning, planning, decision making under uncertainty, and machine learning.
                        <br>
                        <span class="bold">Syllabus Description:</span> This course is an introductory course to artificial intelligence. The purpose of this course is to provide an overview of this field. We will cover topics including: agents, search, uncertainty, and learning. The goals of this course are to provide a fundamental knowledge of the field.  The course evaluation will include homework, a midterm, and a final.
                        <br>
                        <span class="bold">Languages and Tools:</span> Python, numpy, matplotlib, tensorflow
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Gridworld Search</span>: In a simple grid based maze environment, implemented depth first search, breadth first search, A* search, and uniform cost search.</li>
                        <li><span class="bold">Chess Minimax</span>: Implemented alpha-beta pruning on an agent that used minimax to choose its next move in chess.</li>
                        <li><span class="bold">Deep Neural Networl</span>: Created and trained a simple DNN model for chess using tensorflow.</li>
                        <li><span class="bold">AlphaZero Othello</span>: Implemented specifically the monte carlo tree search portion of AlphaZero.</li>
                        <li><span class="bold">Pac-man and Reinforcement Learning</span>: In a simplified, grid based implemention of Pacman, implemented SARSA and Q-Learning algorithms.</li>
                        </ul>`,
                    'eecs497':
                        `<span class="bold">Course Guide Description:</span> Team-based, user-centered design and development of complex software systems incorporating effective design strategies and project management methodologies. Topics include customer discovery, contextual inquiry, prototyping, process models, creative problem solving, inclusive thinking, team dynamics, social concerns, and testing strategies. Teams of 3-5 students complete projects based primarily on their individual interests.
                        <br>
                        <span class="bold">Syllabus Description:</span> "When you design for everyone, you design for no one." This phrase describes one of the key reasons why software design (and development) can quickly lead to failure. In this course, we will address this problem by covering a wide variety of topics that are significant in the creation of great software but that are frequently overlooked.  The end goal of this course is for teams to participate in the complete software design and development cycle, from idea incubation to product delivery, and to create successful software products that incorporate effective design strategies and project management methodologies.
                        <br>
                        <span class="bold">Projects:</span> This course was based around building up a single project throughout the semester as part of a small group. My group implemented a Google Chrome extension that quizzes the user on the definition of words on the page they're currently viewing, in an attempt to be a tool for people trying to learn English.`,
                    'eecs376':
                        `<span class="bold">Course Guide Description:</span> Introduction to theory of computation. Models of computation: finite state machines, Turing machines. Decidable and undecidable problems. Polynomial time computability and paradigms of algorithm design. computational complexity emphasizing NP-hardness. Coping with intractability. Exploiting intractability: cryptography
                        <br>
                        <span class="bold">Syllabus Description:</span> An introduction to Computer Science theory, with applications. Design and analysis of algorithms, including paradigms such as divide-and-conquer and dynamic programming. Fundamentals of computability and complexity — learn to identify what problems a computer cannot solve at all, what problems are unlikely to be efficiently solvable, and how to apply approximations to such problems. Introduction to randomness in computation, including how algorithms can benefit from randomness and how to analyze randomized algorithms. Applications of computational hardness to cryptography, including specific algorithms that are essential to the internet.`,
                    'eecs201':
                        `<span class="bold">Course Guide Description:</span> Essential tools for computer programming: Shells, environments, scripting, Makefiles, compilers, debugging tools, and version control.
                        <br>
                        <span class="bold">Syllabus Description:</span> This is a 1-credit hour seminar designed to teach students the essentials of using a computer effectively for EECS students. While the target audience is EE/CE/CS/DS students, any student wishing to learn how to use their computer more effectively is encouraged to join. Students are expected to be familiar with basic concepts of programming, such as control flow (e.g. if-else, loops, functions) and expressing their ideas in program statements. Topics covered include Unix-like systems, shells, version control, build systems, debugging, and scripting. The ultimate goal of the class is to give students hands on experience with the prevailing tools of the trade as well cultivate research skills for picking up new tools in an ever-evolving computing landscape.
                        <br>
                        <span class="bold">Languages and Tools:</span> Unix, git, bash, shell scripting, regex, vim, python, GDB, valgrind`,
                    'clciv375':
                        `<span class="bold">Course Guide Description:</span> Why do people fight wars? What impact do they have on society? Why do empires fail? These are the central questions that we will be exploring in this course. The course will involve examination of strategic thinking in the ancient and modern worlds (and will include contemporary discussions of military conflict). On the ancient side you will have a chance to explore questions about the nature of war with the aid of some of the greatest minds of the ancient world, and we will encounter some of its most memorable figures, from the crafty Themistocles to Alexander the Great, Hannibal and Julius Caesar with the aid of Herodotus, Thucydides, and Caesar himself (amongst others).
                        <br>
                        <span class="bold">Personal Description:</span> An exploration of classical era warfare, beginning with the Persian war and going through Caesar's conquest of Gaul.
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Caesar, The Gallic War</li>
                        <li>Herodotus, The Histories</li>
                        <li>Polybius, The Rise of the Roman Empire</li>
                        <li>Thucydides, The Peloponnesian War</li>
                        <li>Martin, Ancient Greece: From Prehistoric to Hellenistic Times 2nd Edition</li>
                        <li>Potter, Ancient Rome: A New History 3rd Edition</li>
                        </ul>`,
                    'polsci309':
                        `<span class="bold">Course Guide Description:</span> Population, Equity, and Environmental Change examines contemporary debates over the relationships among population growth, environmental change, and sustainability. Scientists agree that the ecosystems of planet Earth cannot long support the human species at its current levels of consumption. They argue that the human species has made itself a geological force, one whose impact on the planet might be compared to the earthquakes and floods that shaped its topography in prehistoric time. Though there is widespread agreement about the magnitude of this change, there is intense debate about how to characterize the crisis it poses. Some define it in terms of the limits of nature, terming it a mismatch between available resources and human numbers: it should be addressed as a “population problem.” Others define it in political terms as a crisis of distribution and social practice.
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Malthus, An Essay on the Principle of Population</li>
                        <li>Hardin, The Tragedy of the Commons</li>
                        <li>Wallace, Consider the Lobster</li>
                        </ul>`,
                    'tchnclcm300':
                        `<span class="bold">Course Guide Description:</span> Professional communication to the general public, managers, and other professionals about electrical and computer engineering ideas as presented in written reports and oral presentations. Functional, physical, and visual/diagrammatic descriptions; job letters and resumes.
                        <br>
                        <span class="bold">Syllabus Description:</span> In this course, you will learn and practice meaningful principles of technical communication, research, and teamwork. You will work on your own and in groups to complete your assignments. Together, we will work on writing as a process, participate in peer review, and give careful attention to genre and industry standards.`,
                    'polsci302':
                        `<span class="bold">Course Guide Description:</span> This course examines the principal theorists of politics from modernity to the present and the most significant ideas and trends that have shaped the world, which may include race, empire, colonialism, gender, democracy, and violence.
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Hobbes, Leviathan</li>
                        <li>Rousseau, Discourse on the Origin of Inequality</li>
                        <li>Chomin, Discourse of Three Drunkards on Government</li>
                        <li>Nietzche, On the Genealogy of Morality</li>
                        <li>Foucault, Discipline & Punish</li>
                        <li>Schmitt, The Concept of the Political</li>
                        <li>Vitoria, On the American Indians</li>
                        <li>Luther, Concerning Governmental Authority</li>
                        <li>Luther, The Twelve Articles of the Swabian Peasants</li>
                        <li>Luther, On War with the Turk</li>
                        <li>Luther, That Jesus Was Born a Jew</li>
                        <li>Locke, Second Treatise on Government</li>
                        <li>Wollstonecraft, Vindication of the Rights of Women</li>
                        <li>Fanon, On Violence</li>
                        <li>Derrida, Philosophy in a Time of Terror</li>
                        <li>Du Bois, The African Roots of War</li>
                        <li>Du Bois, The Problem of Problems</li>
                        <li>Mills, The Racial Contract</li>
                        <li>Murray, Protest Against the Legal Status of the Negro</li>
                        <li>Murray, Jane Crow and the Law</li>
                        <li>Kanafani Men in the Sun</li>
                        </ul>`,
                    'polsci402':
                        `<span class="bold">Course Guide Description:</span> This course examines the development of liberalism as a political theory of individualism, rights, and toleration. It also considers an array of criticisms of liberalism, some originating within liberalism itself and some coming from anti-liberals. The course will primarily focus on contemporary liberalism and the debates surrounding it, although attention will be given to earlier formulations of liberalism.
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Locke, A Letter Concerning Tolerance</li>
                        <li>Mill, On Liberty</li>
                        <li>Rawls, Justice as Fairness</li>
                        <li>Schmitt, The Concept of the Political</li>
                        <li>Locke, Second Treatise on Government</li>
                        <li>Kant, An Answer to the Question: What is Enlightenment</li>
                        <li>Constant, The Liberty of the Ancients Compared with That of the Moderns</li>
                        <li>Berlin, Two Concepts of Liberty</li>
                        <li>Taylor, What's Wrong with Negative Liberty</li>
                        <li>Shklar, Liberalism of Fear</li>
                        <li>Waldron, Theoretical Foundations of Liberalism</li>
                        <li>Nozick, Anarchy, State, and Utopia</li>
                        <li>Mills, Racial Liberalism</li>
                        <li>Okin, Justice and Gender</li>
                        <li>Scanlon, The Difficulty of Toleration</li>
                        <li>Brown, Tolerance as/in Civilizational Discourse</li>
                        <li>Waldron, Protecting Dignity or Protection from Offense</li>
                        <li>Feinberg, Offensive Nuisances</li>
                        <li>Altman, Liberalism and Campus Hate Speech</li>
                        <li>Rostboll, Autonomy, Respect and Arrogance in the Danish Cartoon Controversy</li>
                        <li>Marx, On the Jewish Question</li>
                        <li>Marx, Economic and Philosophic Manuscripts of 1844</li>
                        <li>Marx, Manifesto of the Communist Party</li>
                        </ul>`,
                    'polsci101':
                        `<span class="bold">Course Guide Description:</span> This course serves as an introduction to political theory, one of the subfields of political science. Political theory traditionally focuses on questions of how to manage our lives together. It concerns itself with questions of how we should live, as individuals and communities, what counts as valuable human life, what justice is, what just and fair institutions look like, and how to govern. The political theory also explores the (sometimes changing) meanings of “democracy,” “liberalism,” and “conservatism."
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Lewis, March</li>
                        <li>Locke, Second Treatise on Government</li>
                        <li>Smith, Wealth of Nations</li>
                        <li>Burke, Reflections on the Revolution in France</li>
                        <li>Marx, Manifesto of the Communist Party</li>
                        <li>Winthrop, A Model of Christian Charity</li>
                        <li>Crevecoeur, Letters from an American Farmer</li>
                        <li>Jefferson, Notes on Virginia</li>
                        <li>Madison, The Federalist #10</li>
                        <li>Paine, Agrarian Justice</li>
                        <li>Mill, On Liberty</li>
                        <li>Tocqueville, Democracy in America</li>
                        <li>Apess, Euology on King Philip, as Pronounced at the Odeon, in Federal Street</li>
                        <li>Douglass, The Meaning of July 4th for the Negro</li>
                        <li>Hammond, Cotton is King</li>
                        <li>Lincoln, Proclamation Suspending the Writ of Habeas Corpus</li>
                        <li>Lincoln, Gettysburg Address</li>
                        <li>Lincoln, Address at Sanitary Fair</li>
                        <li>Sumner, What Social Classes Owe to Each Other</li>
                        <li>Sinclair, The Jungle</li>
                        <li>Schlosser, Fast Food Nation</li>
                        <li>Hayek, The Road to Serfdom</li>
                        <li>Hayek, The Use of Knowledge in Society</li>
                        <li>Roosevelt, Annual Message to Congress 1935</li>
                        <li>Mettler, The Submerged State</li>
                        <li>Buckley, Why the South Must Prevail</li>
                        <li>Goldwater, The Conscience of a Conservative</li>
                        <li>Carmichael, What We Want</li>
                        <li>Rustin, Black Power and Coalition Politics</li>
                        <li>Combahee River Collective, A Black Feminist Statement</li>
                        </ul>`,
                    'ling441':
                        `<span class="bold">Course Guide Description:</span> The term "Computational Linguistics" can refer to several related fields. One is the use of computational methods to investigate the kinds of issues that theoretical linguists are interested in. An example of this is the design of computer-simulated learning algorithms to test how and to what extent a particular grammar and set of underlying forms that account for some linguistic phenomenon is learnable. Another is to use computational methods to solve engineering tasks that involve language such as machine translation, text classification and summarization, information extraction and morphological prediction. Work in this course will involve both of these areas as well as investigating possible interactions between the two. Models based on various kinds of neural networks will also be investigated.
                        <br>
                        <span class="bold">Languages and Tools:</span> Python`,    
                    'polsci352':
                        `<span class="bold">Course Guide Description:</span> Autocrats are in a precarious position. They face threats from the population who want greater democratization and threats from other regime elites who want to replace them. How do autocrats navigate these threats? In this course, we study how to be an autocrat. After understanding the different nature of threats that autocrats face, we then move into the different ways that autocrats design their regimes to temper these threats including coup-proofing their security apparatus, discriminate and indiscriminate violence against the population, and co-optation. The last big theme is how authoritarian regimes are held accountable for their actions.
                        <br>
                        <span class="bold">Syllabus Description:</span> Autocrats are in a precarious position – facing threats from the population who wants greater democratization and other regime elites who want to replace them. How do autocrats navigate these threats? In this course, we study how to be an autocrat. After understanding the different nature of threats that autocrats face, we then move into the different ways that autocrats design their regimes to temper these threats including coup-proofing their security apparatus, coercing the population, and co-opting rival elites. We then move to cases in which (elements of) autocracy co-exists with elections.`,
                    'stats280':
                        `<span class="bold">Course Guide Description:</span> STATS 280 will provide in-depth discussion of models and methods that are appropriate to spectific situations, criteria for selecting among them, their strengths and weaknesses and their conceptual footing. Interactive learning will be emphasized in lectures and the laboratory module. During the lab, students will learn to use modern statistical software for visualization and data analysis, and carry out the computational parts of lab assignments. STATS 280 includes derivations of basic statistical results such as expected values and sampling variances using techniques from pre-calculus mathematics. Students will also be expected to master quantitative relationships such as scaling relationships between variances, sample sizes, and standard errors. Definition and summary of univariate and bivariate data, distributions, correlation, and associated visualization techniques; randomization in comparative studies and in survey sampling; basic probability calculus, including conditional probabilities, concept of random variables and their properties; sampling distributions and the central limit theorem; statistical inference, including hypothesis tests, confidence intervals; one sample and two sample problems with binary and continuous data, including nonparametric procedures; analysis of variance; simple and bivariate regression; simple design of experiments; chisquare and rank-based tests for association and independence.
                        <br>
                        <span class="bold">Languages and Tools:</span> R`,
                    'physics140':
                        `<span class="bold">Course Guide Description:</span> Have you ever wondered why the Moon does not fall onto the Earth, what springs and gravity have in common, why a locomotive can pull a hundred cars while a truck cannot, what is the tallest straw you can use to sip water, or how our spacecraft can leave the solar system without having enough speed to do so when launched from Earth? If you have had these questions – or any other about the natural world around us – then this class is for you! Physics 140 provides a calculus-based introduction to mechanics, the physics of motion. This is the first semester of the Introductory Physics sequence for students in the Natural/Physical Sciences and Engineering. The course explores and applies fundamental principles of classical mechanics to a wide range of physical systems and environments. We learn to construct simple models to describe complex phenomena and answer diverse questions about nature. At the end of the course you will be equipped with a variety of tools and methods to predict motion based on interactions. The main building blocks of the course include kinematics, dynamics (Newton's laws), work and energy, momentum, rotational motion, gravitation, oscillations, waves, and fluids.
                        <br>
                        <span class="bold">Syllabus Description:</span> Physics 140 provides a calculus-based introduction to Classical Mechanics, including waves and fluids. This is the first semester of the Introductory Physics sequence for the Natural Sciences and Engineering.
                        <br>
                        <span class="bold">Languages and Tools:</span> Python`,
                    'physics240':
                        `<span class="bold">Course Guide Description:</span> This is the second semester of the calculus based Introductory Physics sequence for students in the Natural/Physical Sciences and Engineering.  We will develop an understanding of electric charge and currents, how they generate electric and magnetic fields, and the how deeper connections between these fields leads to Electromagnetic waves (aka "light").  We will investigate applications including DC and AC circuits and power transmission.
                        <br>
                        <span class="bold">Syllabus Description:</span> This course will introduce you to the fundamentals of electromagnetism: static electric and magnetic fields, circuits, electromagnetic induction, Maxwell's equations, electromagnetic radiation, and optics.
                        <br>
                        <span class="bold">Languages and Tools:</span> Python`,
                    'polsci389':
                        `<span class="bold">Course Guide Description:</span> This course will explore the relationship between the Legislative and Executive branches of the U.S. government. We will examine the enduring power struggle between the branches as well as contemporary disputes. We will consider the current relations between the Trump White House and the divided Congress. The course will cover the history and changing dynamic of this relationship and will use the historical record, media coverage, and legal cases to understand this conflict. We will also compare a number of contemporary presidents' style, tactics, and constitutional arguments regarding the existence and exercise of presidential powers. This course is in the American government subfield.
                        <br>
                        <span class="bold">Syllabus Description:</span> This course will examine the struggle between the President and Congress to enact public policy. We will study both institutions in isolation and in their interaction. At the end of the course students should understand the following about the battle for power between these two branches. In military metaphors: 1. The War - what the two branches fight about and why. 2. The Arsenals – those powers available to each of the combatants 3. The Rules of Engagement - the Constitution, history, and case law 4. The Battlefield - what you see in the news every day`,
                    'math214':
                        `<span class="bold">Course Guide Description:</span> An introduction to matrices and linear algebra. This course covers the basics needed to understand a wide variety of applications that use the ideas of linear algebra, from linear programming to mathematical economics. The emphasis is on concepts and problem-solving. The sequence 214-215 is not for math majors. It is designed as an alternate to the sequence 215-216 for engineering students who need more linear algebra and less differential equations background.`,
                    'polsci307':
                        `<span class="bold">Course Guide Description:</span> POLSCI 307 will take up one question that political theorists ask: what is the best life? As the U.S. successfully emerged from WWII and eased into “the nifty ‘50's”, consensus opinion held that Americans were enjoying a version of the best life. As incomes rose, some even posited an age of political consensus and “the end of ideology.” However, challenges loomed, and ideological contestation and dissent didn't die after all. Contestation in the post-war era touched on some key categories in political theory: justice, equality, freedom, citizenship, and public voice. Some of the specific issues this class will consider: consumer culture, rural and urban poverty, environmentalism, public health, civil rights, race hierarchy, women's rights, youth culture, the new left, gay rights (as it was then called), neo-conservatism, immigration, the red scare, atomic build-up, the cold war, state secrecy, and the military draft.
                        <br>
                        <span class="bold">Syllabus Description:</span> Political theory is form of analysis. It explores and evaluates the ideas and concepts that shape public life. Political theorists think clearly about political circumstances as they exist and as they might exist. Political theory can also be thought of as a tool to help you better understand the world and a tool that empowers you to act in the world. The class is on American political theory in the post-war era, roughly 1945-1980.
                        <br>
                        <span class="bold">Noteworthy Readings:</span>
                        <ul>
                        <li>Roosevelt, Four Freedoms Address</li>
                        <li>Dewey, Creative Democracy</li>
                        <li>Galbraith, The Affluent Society</li>
                        <li>Harrington, The Other America</li>
                        <li>Carson, Silent Spring</li>
                        <li>Riesman, The Lonely Crowd</li>
                        <li>Friedman, Capitalism and Freedom</li>
                        <li>Rothstein, The Color of Law</li>
                        <li>Satter, Family Properties: Race, Real Estate, and the Exploitation of Black Urban America</li>
                        <li>Schor, Born to Buy: The Commercialized Child and the New Consumer Culture</li>
                        <li>Cheever, The Swimmer</li>
                        <li>King, The Drum Major Instinct</li>
                        <li>Black Panther Party Platform</li>
                        <li>King, I've Been to the Mountaintop</li>
                        <li>Baker, Bigger Than A Hamburger</li>
                        <li>Branch, The King Years: Historic Moments in the Civil Rights Movement</li>
                        <li>Olstad, Cigarette Advertising</li>
                        <li>Blum, Blowing Smoke: The Lost Legacy of the 1964 Surgeon General's Report on Smoking and Health</li>
                        <li>Mills, The Power Elite</li>
                        <li>Hofstadter, The Paranoid Style in American Politics</li>
                        <li>Alinsky, Rules for Radicals</li>
                        <li>Friedan, The Feminine Mystique</li>
                        <li>Coontz, A Strange Stirring</li>
                        <li>Combahee River Collective Statement</li>
                        <li>SNCC, Women in the Movement</li>
                        <li>Moynahan, The Negro Family: A case for national action</li>
                        <li>Faderman, Odd Girls and Twilight Lovers</li>
                        <li>Rich, Diving into the Wreck</li>
                        <li>Lorde, Coal</li>
                        <li>Lorde, A Woman Speaks</li>
                        <li>Issacson, The Innovators</li>
                        <li>Hayden, Port Huron Statement</li>
                        <li>Eisenhower, Farewell Address</li>
                        <li>Hersh, The My Lai Massacre</li>
                        <li>Arendt, Lying in Politics: Reflections on the Pentagon Papers</li>
                        <li>Buckley, Up from Liberalism</li>
                        <li>Lemann, How the Seventies Changed America</li>
                        <li>Packer, The Decade Nobody Knows</li>
                        <li>Rosenfeld, Subversives</li>
                        <li>Carter, At Quang Nam, a Raid and a Reckoning</li>
                        <li>Herring, The Road to Tet</li>
                        <li>Marlantes, Vietnam: The War That Killed Trust</li>
                        <li>Scott, Now It Can Be Told: How Neil Sheehan Got the Pentagon Papers</li>
                        <li>Patterson, Grand Expectations</li>
                        </ul>`,
                    'tchnclcm497':
                        `<span class="bold">Course Guide Description:</span> Advanced technical communication for computer science.  Design and writing of user and task analysis, requirements documents, specifications, proposals, reports and documentation, all aimed at diverse organizational audiences. Preparation and delivery of final oral presentations and written project reports.
                        <br>
                        <span class="bold">Syllabus Description:</span> Technical Communication 497 focuses on technical communication skills in the context of software engineering.`,
                    'engr151':
                        `<span class="bold">Course Guide Description:</span> Algorithms and programming in C++ and MATLAB. Procedural and object-oriented algorithm design, implementation and testing. Emphasis on engineering analysis and embedded computing application.
                        <br>
                        <span class="bold">Syllabus Description:</span> Engineering 151 introduces first-year students to the concept of an algorithm: a well-defined set of instructions that achieve a particular goal. Constructing an algorithm for a given purpose is a fundamental form of engineering design task, and developing computer programs is part of almost every modern engineering project. Students in ENGR 151 learn how to conceptualize algorithms for solving engineering problems and express them in the programming languages C++ and MATLAB. The class is an accelerated version of ENGR 101, covering more of the programming languages and with greater emphasis on programming principles.
                        <br>
                        <span class="bold">Languages and Tools:</span> Matlab, C++, Linux
                        <br>
                        <span class="bold">Projects:</span>
                        <ul>
                        <li><span class="bold">Dam</span>: Matlab assignment dealing with a variety of calculations regarding a hypothetical hydroelectric dam; calculating the volume it contains, create a visualization of the dam, and calculating the maximum power it could generate.</li>
                        <li><span class="bold">MRI</span>: Program that scans through several images of a brain to find, using a threshhold, where a tumor is and plan out the intensity of radiation treatement for it.</li>
                        <li><span class="bold">Rocket Science</span>: A variety of simple simulations of a rocket flight. Maximum velocity absent gravity and wind resistance, maximum distance from earth factoring in gravity, maximum distance from earth factoring in gravity and air resistance, optimal fuel mass for a given thrust and payload in order to reach a given height.</li>
                        <li><span class="bold">Half Life</span>: Code that simulates the decay of radioactive parent nuclei into their children.</li>
                        <li><span class="bold">Heat Spread Simulation</span>: Code that iteratively simulates heat transfer, specifically in order to optimize the position of a space heater in a room. Considerations include the rate the heater outputs energy, window location, and heat spread.</li>
                        <li><span class="bold">K-means Clustering</span>: Implements a simple K-means clustering algorithm.</li>
                        <li><span class="bold">Palindrome</span>: Code that determines whether an input stream is a palindrome or not.</li>
                        </ul>`,
                    'mth215':
                        `This course is a continuation of MTH 114 (Calculus). Topics include integration, applications of integrals, integrals of transcendental functions, inverse functions and hyperbolic trigonometric functions, volumes by integration, curve lengths and surface areas by integration, techniques of integration, conics, polar coordinates, infinite sequences, infinite series, convergence, radius of convergence, and Taylor series.`,
                    'mth216':
                        `This is a continuation of MTH 215. Topics include: Three-dimensional coordinates systems, vectors, dot product, cross product, lines and planes in space, vector valued functions, modeling motion, TNB frame, curvature, multivariable functions, limits and continuity, partial derivatives, chain rule, gradient vectors, tangent planes, extreme values, Lagrange multiple integrals, line integrals, vector fields, work, circulation, flux, potential functions, conservative fields, Green's Theorem, surface area and surface integral, parametrized surfaces, Stokes' Theorem, and the Divergence Theorem.`,
                    'cis205':
                        `This course provides an introduction to essential web development skills. Students will examine the structure and function of the World Wide Web, and the tools used to create and manage web content. Planning, design, and formatting of web pages and sites will be emphasized. Hands on activities include developing web pages incorporating graphics, forms and scripts using Hypertext Markup Language (HTML) and Cascading Style Sheets (CSS). Though there are no specific course pre-requisites, students should have experience in using a web browser, searching, managing computer files and folders, basic word processing and email.
                        <br>
                        <span class="bold">Projects:</span> Included a project that included making a simple travel website for our hometown.
                        <br>
                        <span class="bold">Languages and Tools:</span> JavaScript, HTML, CSS`,
                    'cis260':
                        `This course provides an introduction to fundamental concepts of computer programming, problem solving techniques and algorithm development. Programming projects will emphasize the syntax and usage of a high level programming language, along with analysis, design and testing.
                        <br>
                        <span class="bold">Projects:</span> Text based implementation of the game Uno
                        <br>
                        <span class="bold">Languages and Tools:</span> Java`,
                    'cis261':
                        `This course explores the techniques and data structures used in the development of complex software projects. Through lectures and programming projects, this course will cover elementary data structures, dynamic memory allocation, sorting and searching, recursion, algorithmic analysis and object oriented programming techniques.
                        <br>
                        <span class="bold">Languages and Tools:</span> Java`,
                    'cis252':
                        `This course provides an introduction to technologies and tools used to create dynamic, interactive websites. Client side (browser) technologies will be examined. Programming projects will emphasize the usage of tools such as: JavaScript, AJAX, Silverlight or other technologies as available. This course matches objectives for the CIW JavaScript Specialist Certification.
                        <br>
                        <span class="bold">Languages and Tools:</span> JavaScript, HTML, CSS, AJAX, JSON`,
                    'hs0079':
                        `While, of course, a random speech class in high school is not the most noteworthy, I felt the need to highlight it because one of the skills I think deserves emphasis is my communication skill, and therefore pointing out my taking a course dedicated to rhetoric and the art of public speaking in high school is fairly relevant.`,
                    'hs1050':
                        `AP U.S. History is an introductory college-level U.S. history course. Students cultivate their understanding of U.S. history from c. 1491 CE to the present through analyzing historical sources and learning to make connections and craft historical arguments as they explore concepts like American and national identity; work, exchange, and technology; geography and the environment; migration and settlement; politics and power; America in the world; American and regional culture; and social structures.
                        <br>
                        <span class="bold">AP Exam Score:</span> 3`,
                    'hs1091':
                        `AP U.S. Government and Politics is an introductory college-level course in U.S. government and politics. Students cultivate their understanding of U.S. government and politics through analysis of data and text-based sources as they explore topics like constitutionalism, liberty and order, civic participation in a representative democracy, competing policy-making interests, and methods of political analysis.
                        <br>
                        <span class="bold">AP Exam Score:</span> 5`,
                    'hs2121':
                        `AP Statistics is an introductory college-level statistics course that introduces students to the major concepts and tools for collecting, analyzing, and drawing conclusions from data. Students cultivate their understanding of statistics using technology, investigations, problem solving, and writing as they explore concepts like variation and distribution; patterns and uncertainty; and data-based predictions, decisions, and conclusions.
                        <br>
                        <span class="bold">AP Exam Score:</span> 5`,
                    'hs0163':
                        `AP Seminar is a foundational course that engages students in cross-curricular conversations that explore the complexities of academic and real-world topics and issues by analyzing divergent perspectives. Students learn to investigate a problem or issue, analyze arguments, compare different perspectives, synthesize information from multiple sources, and work alone and in a group to communicate their ideas.
                        <br>
                        <span class="bold">AP Exam Score:</span> 3`,
                    'hs0165':
                        `AP Research, the second course in the AP Capstone experience, allows students to deeply explore an academic topic, problem, issue, or idea of individual interest. Students design, plan, and implement a yearlong investigation to address a research question. Through this inquiry, they further the skills they acquired in the AP Seminar course by learning research methodology, employing ethical research practices, and accessing, analyzing, and synthesizing information. Students reflect on their skill development, document their processes, and curate the artifacts of their scholarly work through a process and reflection portfolio. The course culminates in an academic paper of 4,000–5,000 words (accompanied by a performance, exhibit, or product where applicable) and a presentation with an oral defense.
                        <br>
                        <span class="bold">AP Exam Score:</span> 4`,
                    'hs0069':
                        `AP English Literature and Composition is an introductory college-level literary analysis course. Students cultivate their understanding of literature through reading and analyzing texts as they explore concepts like character, setting, structure, perspective, figurative language, and literary analysis in the context of literary works.
                        <br>
                        <span class="bold">AP Exam Score:</span> 4`,
                    'hs0071':
                        `AP English Language and Composition is an introductory college-level composition course. Students cultivate their understanding of writing and rhetorical arguments through reading, analyzing, and writing texts as they explore topics like rhetorical situation, claims and evidence, reasoning and organization, and style.
                        <br>
                        <span class="bold">AP Exam Score:</span> 5`,
                    'hs5027':
                        `AP Computer Science A is an introductory college-level computer science course. Students cultivate their understanding of coding through analyzing, writing, and testing code as they explore concepts like modularity, variables, and control structures.
                        <br>
                        <span class="bold">Languages and Tools:</span> Java
                        <br>
                        <span class="bold">AP Exam Score:</span> 4`,
                    'hs3079':
                        `AP Chemistry is an introductory college-level chemistry course. Students cultivate their understanding of chemistry through inquiry-based lab investigations as they explore the four Big Ideas: scale, proportion, and quantity; structure and properties of substances; transformations; and energy.
                        <br>
                        <span class="bold">AP Exam Score:</span> 4`,
                    'hs2109':
                        `AP Calculus AB is an introductory college-level calculus course. Students cultivate their understanding of differential and integral calculus through engaging with real-world problems represented graphically, numerically, analytically, and verbally and using definitions and theorems to build arguments and justify conclusions as they explore concepts like change, limits, and the analysis of functions.
                        <br>
                        <span class="bold">AP Exam Score:</span> 5`,
                    'hs5023':
                        `While the non-AP, introductory cs course my high school offered may not be that noteworthy in the grand scheme of things, I list it here to communicate the sheer quantity of CS coursework I went through during high school and college, as well as just how long I've been doing it; I took this class in freshman year. Additionally, this serves as a symbol of how far I've come.
                        <br>
                        <span class="bold">Projects:</span> One noteworthy project I recall was making a functioning version of snake.
                        <br>
                        <span class="bold">Languages and Tools:</span> JavaScript`,
                    'earth106':
                        `<span class="bold">Course Guide Description:</span> This one-credit, half-term minicourse focuses on a few major geological and oceanographic concepts in a lecture setting while also exploring how these same processes are portrayed in popular feature films, both animated and not. This will allow students to learn to reflect critically on the use or misuse of scientific concepts in popular culture, specifically movies. Topics include surface and deep ocean circulation, glacial/interglacial cycles, Earth's internal structure, plate tectonics, volcanoes and earthquakes, coral reefs, life in the ocean, and climate change. A selection of movies that students will be required to watch outside of class time is: The day after tomorrow, The ice age, The core, San Andreas, Pompeii, Finding Nemo, Moana, Waterworld, The Lorax.`,
                    'ala171':
                        `<span class="bold">Course Guide Description:</span> What are you hoping to gain from your time at Michigan? To take advantage of your experience, ALA 171: Making the Most of Michigan, is a 7-week activity-based course designed to help students get involved, meet people, and present themselves professionally to the world at large. Students in the course: Visit and learn about resources across the university campus. Create online portfolios that detail their goals, aspirations, and key experiences and connect them to career networking tools such as LinkedIn, Handshake, and UCAN. Explore social identities and build important communication skills across differences. Grow as leaders in the diverse Michigan community. Facilitated by upper-level students, this course creates an environment for students to share perspectives and ideas. It is recommended for all first-year students. Ultimately, Making the Most of Michigan is making Michigan yours!`,
                    'engr100':
                        `<span class="bold">Course Guide Description:</span> Introduction to Engineering --- Focused team projects dealing with technical, economic, safety, environmental, and social aspects of a real-world engineering problem. Written, oral, and visual communication required within the engineering profession; reporting on the team engineering projects. The role of the engineer in society; engineering ethics. Organization and skills for effective teams.`,
                    'engr110':
                        `<span class="bold">Course Guide Description:</span> Design Your Engineering Experience --- In this elective course, you explore the breadth of opportunities available to engineers in both their education and their career. You will have a chance to explore the foundations of the field, and its influence on ourselves and the world we live in. You will learn about the engineering majors offered at Michigan, and the types of career paths available as an engineer. You will identify your own interests and goals, and discover the broader opportunities available through academic minors and co-curricular opportunities that align with your passions. And, you will actively incorporate this information into a plan for your educational experience in Michigan.`
                }
            }
        }
    }
</script>

